.card-container {
  border: none;
  position: relative;
  transition: transform 1s;
  opacity: 0.65;
}

svg.active {
  color: yellow !important;
  font-size: 30px;
  position: absolute;
  z-index: 1;
}

svg.favorite {
  color: #ccc;
  font-size: 30px;
  position: absolute;
  z-index: 1;
  cursor: pointer;
}

.card-container:hover {
  transform: translate(0, 10px);
  opacity: 1;
}

.cardImg {
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: fixed;
  border-bottom: none;
}

.card-container a:hover {
  text-decoration: none;
}

.card-body-container {
  background: black;
  color: white
}

.badge {
  font-size: 16px;
  margin: -11px auto;
  width: 22%;
  border-radius: 12px;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
}

.movieInfoCard {
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}

.infoImg {
  height: 500px;
  width: 300px;
}

.movieInfoCard h1 {
  color: white;
  text-align: center;
  padding-top: 20px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: bold;
}

.searchInput {
  background-color: black;
  border: 2px solid cyan;
  color: white;
}

.searchInput:focus {
  background-color: black;
  border: 2px solid cyan;
  color: white;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .25);
}

.infoBg {
  width: 80%;
  margin: 0 auto;
  display: block;
  padding-bottom: 30px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}

.swiperImg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: fixed;
  border-bottom: none;
  transform: scale(1);
  transition: transform 1s;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.swiperImg:hover {
  transform: scale(1.1);
}

.swiper_card {
  border: none;
  height: 300px;
  transition: transform 1s;
  opacity: 0.8;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.swiper_card:hover {
  opacity: 1;
}

.detailImg {
  box-shadow: 0px 0px 16px #505054;
  border-radius: 10px;
  height: 500px;
  width: 100%;

}

.swiperBody {
  font-size: 14px;
  color: black;
  padding: 4px;
  height: 70px;
  border-top: none;
  font-family: "serif";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 2;
  background-color: white;
}


.swiper_card:hover .swiperBody {
  background: linear-gradient(to right, rgb(120, 2, 6), rgb(6, 17, 97));
  color: white;
  font-family: "serif";
}

.swiper_card:hover .swiperImg {
  transform: scale(1.1);
  transition: transform 1s;
}

.swiper_card:hover {
  background: linear-gradient(to right, rgb(120, 2, 6), rgb(6, 17, 97));
  color: white;
}

.favTitle {
  color: yellow;
  font-size: 25px;
  position: absolute;
  z-index: 1;
  left: -13px;
  top: 5px;
}

.swiper-button-prev,
.swiper-button-next {
  height: 24px;
}

.infinite-scroll-component {
  overflow: hidden !important;
}